@import url(http://fonts.googleapis.com/css?family=Lato:400,300,700);

#wpwrap,
.wp-toolbar {
  background: #FFFFFF;
}

#wpcontent {
  background: #FFFFFF;
  padding-left: 70px;
  position: relative;
}

#screen-meta,
#screen-meta-links {
  position: relative;
  z-index: 2;
}

#wpfooter {
  display: none;
}

.supapress-wrap {
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  left: 0;
  margin: 0 70px 0 0;
  padding-bottom: 60px;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1;
}

.supapress-header {
  background: $header;
  margin: 0 -70px 52px -70px;
  max-height: 120px;
  overflow: hidden;
  position: relative;
  text-align: center;

  .supapress-header-bg {
    margin: auto;
  }

  .supapress-header-logo {
    fill: #FFFFFF;
    height: 70px;
    left: 50%;
    margin: -32px 0 0 -150px;
    position: absolute;
    top: 50%;
    width: 300px;

    * {
      fill: #FFFFFF;
    }
  }
}

#message {
  background: #FFFFFF;
  border-left: 4px solid #FFFFFF;
  box-shadow: none;
  font-weight: 700;
  margin: 0 0 52px;
  padding: 1px 12px;

  &.deleted,
  &.error {
    border-color: #DD3D36;
  }

  &.created,
  &.updated,
  &.success {
    border-color: #7AD03A;
  }

  &.warning {
    border-color: #FFBA00;
  }

  p {
    font-size: 14px;
    margin: 0.5em 0;
    padding: 2px;
  }
}

@media screen and (max-width: 782px) {
  .supapress-header {
    margin-left: -10px;
  }
}

@media screen and (max-width: 600px) {
  .supapress-wrap {
    top: 46px;
  }
}

.hide {
  display: none;
}

.inline-block {
  display: inline-block;
}

.green-heading {
  color: #222;
  font-size: 24px;
  margin: 0;

  &.small {
    font-size: 18px;
  }
}

%button {
  -webkit-transition: background 0.2s ease-in;
  -moz-transition: background 0.2s ease-in;
  transition: background 0.2s ease-in;
  background: $blue;
  border: none;
  border-radius: 0;
  box-shadow: none;
  color: #FFFFFF;
  cursor: pointer;
  font-size: 16px;
  font-weight: 300;
  height: auto;
  padding: 10px 15px;

  &:hover {
    background: $darkBlue;
    color: #FFFFFF;
  }
}

%autocomplete {
  background: #FFFFFF;
  border: 1px solid #999;
  border-radius: 0;
  box-shadow: none;
  color: #222;
  margin-top: -2px;
  max-height: 200px;
  overflow: auto;

  li {
    font-size: 18px;
    font-weight: 300;
    line-height: 24px;
    outline: none;
    padding: 6px;

    &.ui-state-focus {
      background: $blue;
      color: #FFFFFF;
    }
  }
}

%supapress-inputs {
  -webkit-transition: border-color 0.2s ease-in;
  -moz-transition: border-color 0.2s ease-in;
  transition: border-color 0.2s ease-in;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: #FFF;
  border: 1px solid #CCC;
  border-radius: 0;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1) inset;
  color: #404040;
  font-size: 18px;
  line-height: 24px;
  margin: 0;
  outline: none;
  padding: 8px 12px;
  width: 280px;

  &:focus {
    border: 1px solid #999;
  }

  &::-ms-clear {
    display: none;
  }
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

.select2-container {
  width: 280px !important;

  .select2-search__field {
    background: #FFF;
    color: $blue;
    box-shadow: none;
    padding: 4px 6px;

    &:focus {
      box-shadow: none;
    }
  }

  .select2-selection--single {
    border-radius: 0;
    cursor: pointer;
    height: 40px;
    outline: none;
    position: relative;
    background: #FFF;
    border: 1px solid #CCC;
    margin: 0;
    width: 280px !important;

    .select2-selection__rendered {
      color: $blue;
      padding: 8px 40px 8px 12px;
      font-size: 18px;
      line-height: 24px;
      width: 85%;
    }

    .select2-selection__arrow {
      border: 6px solid;
      border-color: $blue transparent transparent;
      display: block;
      height: 0;
      position: absolute;
      right: 12px;
      top: 18px;
      width: 0;

      b {
        display: none;
      }
    }
  }

  .select2-search--dropdown {
    padding: 4px 6px 4px 4px;
  }

  .select2-dropdown {
    border: 1px solid #CCC;
    border-top: none;
    color: #404040;
    font-size: 17px;
    line-height: 24px;
    font-weight: 300;
    background: #FFF;

    &.select2-dropdown--above {
      border-top: 1px solid #CCC;
      border-bottom: none;
    }

    .select2-results__option {
      background: #FFF;
      padding: 6px 12px;
      margin: 0;

      &[aria-selected] {
        background: #FFF;
      }

      &.select2-results__option--highlighted,
      &:hover {
        background: $blue;
      }

      &[aria-disabled] {
        background: #FFF;
      }
    }
  }
}

.save-button-wrapper {
  margin-top: 25px;

  .save-button {
    @extend %button;

    background: $blue;

    &:hover {
      background: $darkBlue;
      color: #FFFFFF;
    }
  }

  .next-button {
    display: inline-block;
  }
}

.add-new-button {
  background: $blue;

  &:hover {
    background: $darkBlue;
    color: #FFFFFF;
  }
}

.upload-button {
  @extend %button;
  margin-left: 20px;
}

#supapress-clear-cache-button {
  @extend %button;
}

#supapress-add-bulk-isbns-button {
  @extend %button;
  position: relative;
  top: -11px;
  margin-left:20px;
}

#isbn_lookup_bulk {
  height:200px
}

.supapress-error,
.supapress-error * {
  color: #DD3D36 !important;
}

.wp-admin {
  .supapress-field-wrapper {
    margin-bottom: 25px;
  }

  .supapress-tooltip-wrapper {
    overflow: hidden;
    position: relative;

    span {
      float: left;
    }

    label span.supapress-tooltip-icon,
    span.select2
    {
      float: none;
      display: inline-block;
    }

    .supapress-tooltip-icon {
      cursor: pointer;
      display: block;
      height: 40px;
      text-align: center;
      width: 40px;

      svg {
        height: 20px;
        margin-top: 10px;
        width: 20px;

        * {
          fill: #637170;
        }
      }
    }
  }

  .supapress-label,
  .supapress-paragraph {
    color: #222;
    display: inline-block;
    font-size: 18px;
    line-height: 40px;
    margin: 0;
    vertical-align: top;
    width: 330px;
  }

  .supapress-paragraph {
    width: auto;

    a, a:visited {
      color: $primary;

      &:hover {
        color: $secondary;
      }
    }
  }

  .supapress-input {
    @extend %supapress-inputs;
  }

  .supapress-input {
    &.ui-autocomplete-loading {
      background: #FFF url('../img/predictive-loading.gif') no-repeat center right 12px;
    }

    &:focus {
      box-shadow: none;
      outline: none;
    }
  }

  .supapress-autocomplete {
    @extend %autocomplete;

    max-width: 500px;

    li {
      white-space: normal;
    }
  }

  .supapress-ui-tooltip, .supapress-tooltip-arrow:after {
    background: #23282D;
  }

  .supapress-ui-tooltip {
    color: #FFFFFF;
    max-width: 300px;
    padding: 10px;
    position: absolute;
    text-align: center;
    z-index: 99999;
  }

  .supapress-tooltip-arrow {
    width: 70px;
    height: 16px;
    overflow: hidden;
    position: absolute;
    left: 50%;
    margin-left: -33px;
    bottom: -16px;

    &.top {
      top: -16px;
      bottom: auto;

      &:after {
        bottom: -20px;
        top: auto;
      }
    }

    &:after {
      content: "";
      position: absolute;
      left: 20px;
      top: -20px;
      width: 25px;
      height: 25px;
      box-shadow: 6px 5px 9px -9px black;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg);
    }
  }
}

.supapress-accordion-header {
  -webkit-transition: all 0.4s ease-in;
  -moz-transition: all 0.4s ease-in;
  transition: all 0.4s ease-in;
  border: 1px solid #CCC;
  box-sizing: border-box;
  cursor: pointer;
  font-size: 18px;
  line-height: normal;
  margin: 10px 0;
  padding: 5px;
  width: 30%;

  &.open {
    background: #E8E8E8;
    width: 60%;
  }

  &:hover {
    background: #E8E8E8;
  }

  svg {
    fill: $blue;
    float: left;
    height: 10px;
    width: 10px;
  }

  span.svg-right-arrow {
    -webkit-transition: transform 0.2s ease-in;
    -moz-transition: transform 0.2s ease-in;
    transition: transform 0.2s ease-in;
    display: inline-block;
    height: 10px;
    margin: 7px 2px 0 0;
    transform-origin: 50% 50% 0px;
    vertical-align: top;
    width: 10px;

    &.open {
      transform: rotate(90deg);
    }
  }
}

.supapress-accordion-content {
  .supapress-accordion-content-inner {
    padding: 15px 0 2px;

    .supapress-label {
      float: left;
      width: 30% !important;
    }

    .supapress-field-wrapper {
      box-sizing: border-box;
      overflow: hidden;
      padding-left: 15px;
      width: 60%;
    }

    .select2-container {
      width: 70% !important;

      .selection,
      .select2-selection--single {
        width: 100% !important;
      }
    }
  }
}

.supapress-shortcode {
  font-size: 90%;
  box-shadow: none;
  color: #404040;
  border: 1px solid #ddd;
  font-weight: 300;
  width:40%;
}
