@import "navtabs";
@import "asmselect";
@import "switches";

.title-wrapper {
  color: #999;
  margin-bottom: 60px;
  max-width: 100%;
  width: 315px;
}

.list-title {
  background: none;
  border: none;
  box-shadow: none;
  color: #404040;
  font-size: 24px;
  line-height: 1.4em;
  outline: none;
  width: 100%;
}

#widget-title {
  width: 50%;
}

.widget-type-wrapper,
.widget-layout-wrapper {
  overflow: hidden;
  margin: 20px 0 0 0;

  input {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    display: none;
    margin: 0;
    padding: 0;
  }
}

.widget-layout-wrapper {
  border-bottom: 1px solid #CCC;
  margin-bottom: 35px;
  margin-left: -10px;
  margin-top: 15px;
  padding-left: 10px;
  width: 40%;
}

.widget-type-wrapper input:checked + .widget-type-label,
.widget-layout-wrapper input:checked + .widget-layout-label {
  cursor: default;

  .widget-layout-text {
    border: 1px solid #CCC;
    border-bottom: none;
  }

  .svg-icon * {
    fill: #222;
  }

  &:hover {
    .widget-layout-text {
      background: #FFF;
    }
  }
}

.widget-layout-wrapper-inner {
  float: left;
  margin: 0;
}

.widget-type-label,
.widget-layout-label {
  cursor: pointer;
  color: $blue;
  font-family: 'Lato', sans-serif;
  font-weight: 300;
  font-size: 18px;
  text-align: center;
  display: inline-block;
  float: left;
  padding: 0;
  -webkit-transition: border 0.2s ease-in;
  -moz-transition: border 0.2s ease-in;
  transition: border 0.2s ease-in;
  background: #FFFFFF;
  border: none;

  &:hover {
    .widget-layout-text {
      border: 1px solid #E5E5E5;
      border-bottom: none;
      background: #E5E5E5;
    }
  }

  &.widget-type-label {
    margin: 0 4px 4px 0;
  }
}

.widget-type-label .label-content .svg-icon,
.widget-layout-label .label-content .svg-icon {
  position: absolute;
  fill: #FFFFFF;
  left: 50%;
  margin: -90px 0 0 -45px;
  top: 50%;
  width: 90px;

  * {
    -webkit-transition: fill 0.2s ease-in;
    -moz-transition: fill 0.2s ease-in;
    transition: fill 0.2s ease-in;
    fill: #999;
  }
}

.widget-type-label .label-content,
.widget-layout-label .label-content {
  background: #FFF no-repeat center 20px;
  width: 150px;
  height: 50px;
  display: block;
  padding-top: 95px;
  position: relative;
  -webkit-transition: color 0.2s ease-in;
  -moz-transition: color 0.2s ease-in;
  transition: color 0.2s ease-in;
}

.widget-type-label:last-child,
.widget-layout-label:last-child {
  margin-right: 0;
}

.widget-type-label img,
.widget-layout-label img {
  display: block;
  margin: auto;
  padding-top: 22px;
}

.widget-layout-label {
  .label-content {
    height: 100px;
    padding: 0;
    width: 100px;

    .svg-icon {
      height: 150px;
      margin: -76px 0 0 -35px;
      width: 70px;
    }
  }

  .widget-layout-label {
    -webkit-transition: color 0.2s ease-in;
    -moz-transition: color 0.2s ease-in;
    transition: color 0.2s ease-in;
  }

  .widget-layout-text {
    -webkit-transition: all 0.2s ease-in;
    -moz-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
    border: 1px solid #FFF;
    border-bottom: none;
    color: $blue;
    display: block;
    padding: 10px 15px;
  }
}

#step1 {
  .widget-type-field-wrapper {
    border-top: 1px solid #CCC;
    margin-top: 40px;
    padding-top: 30px;
    width: 50%;
  }

  .widget-type-wrapper {
    margin: 0;
    padding-left: 10px;
  }

  .widget-type-label {
    margin: 0 calc((100% - (#{$widget-icon-width} * #{$number-of-widgets})) / (#{$number-of-widgets} - 1)) 0 0;

    &:hover {
      .widget-type-text {
        border: 1px solid #E5E5E5;
        background: #E5E5E5;
      }
    }

    &.details {
      margin: 0;
    }

    .label-content {
      width: $widget-icon-width;
    }
    
    .widget-type-text {
      -webkit-transition: all 0.2s ease-in;
      -moz-transition: all 0.2s ease-in;
      transition: all 0.2s ease-in;
      border: 1px solid #FFF;
      color: $blue;
      display: block;
      padding: 10px 15px;
    }
  }

  .widget-type-wrapper input:checked + .widget-type-label {
    &:hover {
      .widget-type-text {
        border: 1px solid #CCC;
        background: #FFF;
      }
    }

    .widget-type-text {
      border: 1px solid #CCC;
      position: relative;
    }

    .svg-icon * {
      fill: #222;
    }
  }
}

.widget-layout-wrapper input:checked + .widget-layout-label {
  cursor: default;
}

.edit-collection-button,
.add-new-button {
  @extend %button;

  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin-left: 20px;
  text-decoration: none;
  vertical-align: middle;

  &:hover, &:active, &:visited, &:focus {
    box-shadow: none;
    color: #FFFFFF;
  }
}

#step2 {
  position: relative;

  .widget-type-wrapper {
    -webkit-transition: all 0.5s;
    -moz-transition: all 0.5s;
    transition: all 0.5s;
    background: #FFFFFF;
    border: 1px solid #FFFFFF;
    max-height: 50px;
    margin: 0;
    padding: 15px;
    position: absolute;
    right: -15px;
    top: -15px;
    width: 200px;
    z-index: 99;

    input:checked + .widget-type-label {
      cursor: pointer;

      .label-content {
        border: 1px solid $primary;
      }

      .widget-type-text {
        color: $primary;
      }
    }

    &.open {
      border-color: $blue;
      max-height: 1000px;

      input:checked + .widget-type-label {
        cursor: default;
      }
    }

    .widget-type-label {
      border: none;
      clear: right;
      display: block;
      float: right;
      margin: 0 0 15px;
      padding: 0;

      &:last-child {
        margin: 0;
      }

      &:hover {
        .label-content {
          border: 1px solid $blue;
        }
      }
    }

    .svg-icon * {
      fill: $blue;
    }

    input:checked + .widget-type-label {
      .svg-icon * {
        fill: $primary;
      }
    }

    .label-content {
      height: 50px;
      padding: 0;
      width: 50px;
      border: 1px solid #FFF;
    }

    .widget-type-text {
      color: $blue;
      display: block;
      font-size: 14px;
      line-height: 50px;
      margin: 0 70px 0 -150px;
      text-align: right;
    }

    .svg-icon {
      height: 34px;
      margin: -17px 0 0 -45px;
    }
  }
}

.lookup-source-input {
  margin-top: 25px;
}

.sub-content-wrapper .supapress-label {
  box-sizing: border-box;
  padding-left: 50px;
}

.lazy-load,
.show-arrows {
  position: relative;

  input, label, img {
    display: inline-block;
    vertical-align: middle;
  }

  .image-preview {
    bottom: 0;
    cursor: pointer;
    margin: auto;
    max-height: 40px;
    max-width: 40px;
    position: absolute;
    top: 0;
  }

  .placeholder-upload,
  .arrow-upload {
    margin-left: 60px;
  }

  .placeholder-upload,
  .arrow-upload,
  .upload-button {
    visibility: hidden;
  }
}