.supapress-isbn-lookup-asm,
.supapress-search-restriction-asm
{
  display: none !important;
}

.asmHighlight {
  font-weight: 700;
}

.asmContainer {
  margin-top: 25px;

  .asmListItem {
    background: #FFF;
    border: 1px solid #CCC;
    box-sizing: border-box;
    color: #222;
    font-weight: 300;
    font-size: 14px;
    margin: 0 0 10px;
    padding: 8px 12px;
    width: 60%;

    &.book-not-found,
    &.book-not-found a {
      background: #DD3D36;
      color: #FFFFFF;
    }
  }

  .asmListItemLabel {
    padding-left: 20px;
  }

  .asmListItemRemove {
    background: $blue;
    border: 1px solid $blue;
    color: #FFFFFF;
    display: block;
    font-weight: 300;
    height: 20px;
    line-height: 16px;
    float: left;
    padding: 0;
    right: 10px;
    text-align: center;
    text-decoration: none;
    top: 10px;
    width: 20px;

    &:hover {
      background: $primary;
      border: 1px solid $primary;
      color: #FFFFFF;
    }
  }

  .three-squares {
    cursor: move;
    display: block;
    background: #555;
    height: 5px;
    left: 14px;
    pointer-events: none;
    position: absolute;
    top: 18px;
    width: 5px;

    &::before, &::after {
      background: #555;
      content: '';
      display: block;
      height: 5px;
      left: 0;
      position: absolute;
      width: 5px;
    }

    &::before {
      top: -7px;
    }

    &::after {
      top: 7px;
    }
  }

  .book-not-found {
    border: none;

    .asmListItemRemove {
      border: 1px solid #FFFFFF;

      &:hover {
        background: #FFFFFF;
        color: #DD3D36;
      }
    }

    .three-squares,
    .three-squares:before,
    .three-squares:after {
      background: #FFFFFF;
    }
  }
}