/* List page */
.fixed .column-title {
  max-width: 32%;
}

.fixed .column-type {
  width: 10%;
}

input.shortcode-in-list-table {
  width: 100%;
  font-size: 90%;
}

.supapress-wrap {
  .list-page-title {
    margin: 0;
  }

  #search-submit,
  #widget-type-selector-submit {
    @extend %button;

    float: left;
    height: 42px;
    line-height: 42px;
    padding: 0 15px;
  }

  .search-term-wrapper {
    margin: 10px 0 35px;

    .subtitle {
      color: $primary;
      font-weight: 700;
      padding: 0;
    }
  }

  #supapress-widget-search-input {
    float: left;
    height: 42px;
    margin: 0;
  }

  .add-new-wrapper {
    padding-top: 30px;

    .add-new-button {
      margin: 0;
    }
  }

  .tablenav-pages {
    height: auto;
    margin: 0;
  }

  .displaying-num {
    color: #637170;
    font-size: 18px;
    font-weight: 300;
  }

  .pagination-links {
    color: #637170;
    font-size: 18px;
    font-weight: 300;

    a {
      @extend %button;

      font-size: 30px;
      font-weight: 300;
      height: 30px;
      line-height: 25px;
      margin: 0;
      padding: 0 10px;
      text-align: center;
      vertical-align: top;
      width: auto;
    }

    span.paging-input {
      line-height: 30px;

      input {
        @extend %supapress-inputs;

        font-size: 14px;
        font-weight: 300;
        height: 30px;
        line-height: 25px;
        margin: 0;
        padding: 0 10px;
        text-align: center;
        vertical-align: top;
        width: auto;
      }
    }

    span.tablenav-pages-navspan {
      @extend %button;

      background: #CCC;
      cursor: default;
      font-size: 30px;
      font-weight: 300;
      height: 30px;
      line-height: 25px;
      margin: 0;
      padding: 0 10px;
      text-align: center;
      vertical-align: top;
      width: auto;

      &:hover {
        background: #CCC;
      }
    }
  }

  .tablenav {
    overflow: hidden;

    &.top {
      margin: 0 0 10px;
    }

    &.bottom {
      margin: 10px 0 0;
    }

    #doaction,
    #doaction2 {
      margin: 0;
    }

    .select2-container {
      width: 200px !important;
      margin-right: 2px;
    }

    .select2-selection--single {
        height: 30px;
        width: 200px !important;
    }

    .select2-selection__rendered {
      padding: 0 40px 0 14px;
      font-size: 14px;
      line-height: 30px;
    }

    .select2-selection__arrow {
      top: 12px;
    }

    .actions.bulkactions,
    .actions.widgettype {
      padding: 0;
      margin-right: 10px;
    }

    .button.action {
      @extend %button;

      font-size: 14px;
      font-weight: 300;
      height: 30px;
      margin: 0;
      padding: 0;
      width: 70px;
    }
  }

  .wp-list-table {
    border: none;
    border-spacing: 1px 1px;

    thead, tfoot {
      color: #404040;

      tr {
        border: none;
      }

      a, th, td {
        border: none;
        box-shadow: none;
        color: #404040;
      }

      th, td {
        background: #F1F1F1;
        font-weight: 700;
      }
    }

    tbody {
      background: #FFF;
      color: #404040;

      tr {
        background: #FFF;
        color: #404040;

        &:nth-child(even) {
          background: #F5F5F5;
        }

        .row-title {
          font-weight: 400;
        }
      }

      a, td, input, span {
        box-shadow: none;
        color: #404040;
      }

      a:hover {
        text-decoration: underline;
      }

      abbr[title] {
        text-decoration: none;
      }
    }
  }
}

.select2-container {
  .list-page-select2 {
    font-size: 13px;
  }
}