.wp-admin {
  h2.nav-tab-wrapper {
    border-bottom-color: #CCC;
    margin-bottom: 25px;
    margin-left: -10px;
    min-height: 40px;
    overflow: hidden;
    padding: 15px 0 0 10px;

    .nav-tab {
      -webkit-transition: all 0.2s ease-in;
      -moz-transition: all 0.2s ease-in;
      transition: all 0.2s ease-in;
      background: #FFF;
      border: 1px solid #FFF;
      box-shadow: none;
      color: $blue;
      display: block;
      float: left;
      font-size: 18px;
      font-weight: 300;
      line-height: 18px;
      margin: -4px 0px -1px;
      padding: 10px 15px 15px;
      position: relative;
      text-align: center;

      &:hover {
        background: #E5E5E5;
        border: 1px solid #E5E5E5;
      }

      &.nav-tab-active {
        border: 1px solid #CCC;
        border-bottom: none;
        box-shadow: none;
        color: $blue;
        cursor: default;

        &:hover {
          background: #FFF;
        }

        &:after {
          background: $blue;
          bottom: 0;
          content: "";
          display: block;
          height: 5px;
          left: -1px;
          position: absolute;
          right: -1px;
        }
      }
    }
  }
}