.elements.nav-tab-content {
  overflow: hidden;

  .config {
    box-sizing: border-box;
    float: left;
    padding-right: 20px;
    width: 65%;

    .element-config {
      .supapress-field-wrapper {
        margin-bottom: 10px;
        position: relative;
      }

      .supapress-label {
        padding: 0;
        width: 304px;
      }

      .asmContainer {
        margin-top: 38px;
      }

      .asmHighlight {
        position: absolute;
        right: 40%;
        top: 50px;
      }
    }
  }

  .selector {
    border-left: 1px solid rgb(153, 153, 153);
    box-sizing: border-box;
    float: right;
    max-height: 500px;
    max-width: 395px;
    overflow: auto;
    padding: 0 20px;
    width: 35%;

    .onoffswitch {
      float: right;
    }

    .supapress-field-wrapper {
      margin: 0;
    }

    .supapress-label {
      width: 205px;
    }
  }
}