.wp-admin .settings {
  input[type="text"],
  .select2-container,
  .widget_link_preview {
    width: 40% !important;
  }

  .select2-container {
    .selection,
    .select2-selection--single {
      width: 100% !important;
    }
  }

  .select2-container {
    .selection,
    .select2-selection--single {
      width: 100% !important;
    }
  }

  .supapress-cache-lifetime {
    .select2-container {
      .selection,
      .select2-selection--single {
        width: 280px !important;
      }
    }
  }

  .supapress-domain-slug {
    background: rgba(0, 0, 0, 0.07) none repeat scroll 0 0;
    font-size: 14px;
    margin: 0 10px 0 1px;
    padding: 3px 5px 2px;
    float:left;
  }

  .supapress-label {
    width: 20% !important;
  }

  .widget_link_preview {
    background: #FFFFFF;
    border: 1px solid $blue;
    box-sizing: border-box;
    color: #404040;
    padding: 0 12px;
    text-indent: 0 !important;
    width: 60% !important;

    .placeholder {
      color: $blue;
    }
  }

  .supapress-accordion-header {
    padding: 9px;

    &.open {
      width: 80%;
    }
  }

  .supapress-accordion-content-inner {
    padding: 2px 0;

    .supapress-field-wrapper {
      box-sizing: border-box;
      overflow: hidden;
      padding-left: 15px;
      width: 80%;
    }

    .supapress-label {
      float: left;
      width: 40% !important;
    }

    .widget_link_pattern {
      width: 100% !important;
    }

    input[type="text"]:not(.widget_link_pattern),
    .select2-container,
    .widget_link_preview,
    .widget_input_60 {
      width: 60% !important;
    }
  }

  .supapress-javascript-code {
    background: #2d2d2d none repeat scroll 0 0;
    margin: 0.5em 0;
    overflow: auto;
    padding: 1em;
    border-radius: 4px;
    color: #ccc;
    display: block;
    width: 60%;

    .keyword {
      color: #cc99cd;
    }

    .operator {
      color: #67cdcc;
    }

    .function {
      color: #f08d49;
    }

    .punctuation {
      color: #ccc;
    }

    .string {
      color: #7ec699;
    }
  }

  .supapress-link-pattern-wrapper {
    overflow: hidden;
    input: {
      width: 100% !important;
    }
  }

  .supapress-links-wrapper {
    width: 60%;
    line-height: 36px;
    overflow: hidden;
  }
}

.supafolio_page_supapress-settings {
  .supapress-autocomplete {
    max-width: initial;
  }
}

@media only screen and (max-width : 1230px) {
  .wp-admin .settings {
    .supapress-accordion-header.open {
      width: 100%;
    }

    .supapress-accordion-content-inner {
      .supapress-field-wrapper {
        padding: 0;
        width: 100%;
      }

      input[type="text"],
      .select2-container,
      .widget_link_preview,
      .supapress-label {
        width: 100% !important;
      }
    }

    input[type="text"],
    .select2-container,
    .widget_link_preview,
    .supapress-label {
      width: 100% !important;
    }
  }

  .supapress-links-wrapper {
    width: 100% !important;
  }
}

@media only screen and (max-width : 1025px) {
  .wp-admin .settings {
    .supapress-javascript-code {
      width: 100%;
    }
  }
}